import React, { useState } from "react";

export default function StepFinal({
  onRestart,
  onPrev,
}: {
  onRestart: () => void;
  onPrev: () => void;
}) {
  const [hint, setHint] = useState(false);
  const bursts = Array.from({ length: 14 });
  return (
    <div className="final-wrap">
      <div className="final-fireworks">
        {bursts.map((_, i) => (
          <span
            key={i}
            className="burst"
            style={{
              left: `${10 + (i % 7) * 12}%`,
              top: `${10 + (i % 5) * 16}%`,
              animationDelay: `${(i % 7) * 0.2}s`,
            }}
          />
        ))}
      </div>
      <h2 className="title"></h2>
      <div className="final-photo">
        <img src={require("../imgs/34.jpg")} alt="" />
      </div>
      {hint && (
        <div className="gift-hint">
          
        </div>
      )}
      <div className="marquee">
        <div className="marquee-track">
           Happy Birthday  1994     Love U   Happy
          Birthday  1994     Love U  
        </div>
      </div>
      <div className="actions">
        <button className="btn" onClick={onPrev}>
          
        </button>
        <button className="btn primary" onClick={() => setHint(true)}>
          
        </button>

        <button className="btn" onClick={onRestart}>
          
        </button>
      </div>
    </div>
  );
}
