import React, { useMemo, useState } from "react";
import { useBeep } from "./hooks";

export default function StepTimeline({
  onNext,
  onPrev,
}: {
  onNext: () => void;
  onPrev: () => void;
}) {
  const [index, setIndex] = useState(0);
  const img = (n: number) => require(`../imgs/${n}.jpg`);
  const items = useMemo(
    () => [
      {
        title: "",
        text: " DNA",
        icon: "",
        img: img(1),
      },
      {
        title: "",
        text: " ",
        icon: "",
        img: img(2),
      },
      {
        title: "",
        text: "",
        icon: "",
        img: img(3),
      },
      {
        title: "",
        text: "",
        icon: "",
        img: img(4),
      },
      {
        title: "",
        text: " ",
        icon: "",
        img: img(5),
      },
      {
        title: "",
        text: "",
        icon: "",
        img: img(6),
      },
      {
        title: "",
        text: " ",
        icon: "",
        img: img(7),
      },
      {
        title: "ing",
        text: " ",
        icon: "",
        img: img(8),
      },
      {
        title: "",
        text: "",
        icon: "",
        img: img(9),
      },
    ],
    []
  );
  const beep = useBeep();
  const prevNode = () => setIndex((i) => Math.max(0, i - 1));
  const nextNode = () => setIndex((i) => Math.min(items.length - 1, i + 1));
  return (
    <div>
      <h2 className="title"></h2>
      <div className="timeline-wrap">
        <div
          className="nodes"
          style={{
            width: `${items.length * 100}%`,
            transform: `translateX(-${index * (100 / items.length)}%)`,
            transition: "transform .3s ease",
          }}
        >
          {items.map((it, i) => (
            <div
              className="node"
              key={i}
              style={{ flex: `0 0 ${100 / items.length}%` }}
            >
              <div
                className="node-card"
                data-active={i === index ? "true" : "false"}
              >
                <div className="node-icons">{it.icon}</div>
                <h3>{it.title}</h3>
                <p className="subtitle">{it.text}</p>
                <div className="node-photo">
                  <img src={it.img} alt={it.title} />
                </div>
              </div>
            </div>
          ))}
        </div>
        <div className="pager">
          <button
            className="btn"
            onClick={() => {
              beep(500, 100);
              prevNode();
            }}
          >
            
          </button>
          <button
            className="btn"
            onClick={() => {
              beep(520, 100);
              nextNode();
            }}
          >
            
          </button>
        </div>
      </div>
      <div className="actions">
        <button className="btn" onClick={onPrev}>
          
        </button>
        <button className="btn primary" onClick={onNext}>
          
        </button>
      </div>
    </div>
  );
}
