interface Recommendation {
  name: string;
  type: "attraction" | "food";
  coordinates: [number, number];
  description: string;
  details: string;
  shops?: {
    name: string;
    address: string;
    coordinates: [number, number];
    specialties: string[];
  }[];
}

interface Highlight {
  name: string;
  description: string;
  details: string;
  tips?: string;
}

interface TourDay {
  day: number;
  date: string;
  city: string;
  activities: string[];
  highlights: string[];
  coordinates: [number, number];
  transportation?: string;
  attractions?: {
    name: string;
    coordinates: [number, number];
    description: string;
    details?: string;
    openTime?: string;
    ticketPrice?: string;
    highlights?: string[];
    tips?: string;
  }[];
  recommendations?: Recommendation[];
}

