const MapController: React.FC<{ selectedDay: TourDay | null }> = ({
  selectedDay,
}) => {
  const map = useMap();

  useEffect(() => {
    if (selectedDay && selectedDay.attractions) {
      // 
      map.setView(selectedDay.coordinates, 12, { animate: true });
    } else {
      // 
      map.setView([26.5, 117.5], 6, { animate: true });
    }
  }, [selectedDay, map]);

  return null;
};

